pre {
  background: $ui-01;
  color: $text-02;
  margin: 0.5em 0;
  padding: 1em 1.8em;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  line-height: 1.6;
  max-width: 100%;
  page-break-inside: avoid;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
}

kbd {
  background: $ui-05;
  color: $text-04;
  padding: 1px 4px;
  border: none;
  border-radius: 3px;
  line-height: 2;
}
